const itemLists=['仅自己可见','所有人可见']
import uniNavBar from "../../components/uni-nav-bar/uni-nav-bar.vue";
import uploudImges from "../../components/common/uploud-imges.vue"
export default {
	components:{
		uniNavBar,
		uploudImges
	},
	data() {
		return {
			yinsi:1,
			value:'',
			imglist:[],
			isshow:true,
			isget:false,
			postclass:{
								id:0,     // 当前选中分类id
								title:"", // 当前选中分类名称
								range:[], // 可选项
								list:[]   // 服务端获取到的分类列表
							},
							topic:{
								id:0,	  // 当前选中话题id
								title:""  // 当前选中话题名称
							}
		}
	},
	computed:{
		gettype(){
			return changelook[this.yinsi]
		}
	},
	methods: {
		back(){
			uni.navigateBack({
				delta: 1
			});
		},
		submit(){
			console.log('发不成功')
		},
		changelook(){
			uni.showActionSheet({
				itemList: itemLists,
				success: res => {
					this.yinsi=itemLists[res.tapIndex]
				}
			});
		},
		uploud(index){
			this.imglist=index
		},
		lodshow(){
			this.isshow=!this.isshow
		}
	},
	onBackPress(){//监听官方顶部导航的返回事件
		if(!this.value && this.imglist.length<1){
			return;
		}
		if(!this.isget){
			uni.showModal({
				content: '是否保存为草稿',
				cancelText: '不保存',
				confirmText: '保存',
				success: res => {
					if(res.confirm){
						console.log('保存')
					}else{
						console.log('不保存')
					}
					this.isget=true
					uni.navigateBack({delta:1});
				},
			});
			return true
		}
	}
}
